import { Form, Input, Button, notification } from 'antd'
import { UserOutlined, UnlockTwoTone } from '@ant-design/icons'
// import { MONGO_URI_BE } from '../../environment'
import { Client } from '../../commonFunction/apolo';
import { LOGIN } from '../gql';

const LoginArea = ({ props }) => {
  const onFinish = async (values) => {
    try {
      console.log('Success:', values)
      const resultLogin = await Client.query({
        query: LOGIN,
        variables: { input: { userName: values.username, password: values.password } }
      })
      console.log({input: { userName: values.username, password: values.password }})
      if (resultLogin.data && resultLogin.data.login) {
        notification.success({
          message: 'Đăng nhập thành công',
          description: 'Thành công',
          placement: 'bottomRight'
        })
        localStorage.setItem('token', resultLogin.data.login)
        props.setLogin(true)
      } else {
        notification.error({
          message: 'Tên đăng nhập hoặc mật khẩu không đúng',
          description: 'Tên đăng nhập hoặc mật khẩu không đúng',
          placement: 'bottomRight'
        })
      }
    } catch {
      notification.error({
        message: 'Tên đăng nhập hoặc mật khẩu không đúng',
        description: 'Tên đăng nhập hoặc mật khẩu không đúng',
        placement: 'bottomRight'
      })
    }
  };

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <Form
      style={{ padding: "5%" }}
      name="basic"
      initialValues={{
        remember: true,
      }}
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
    >
      <Form.Item
        label="Username"
        name="username"
        rules={[
          {
            required: true,
            message: 'Please input your username!',
          },
        ]}
      >
        <Input prefix={<UserOutlined />} />
      </Form.Item>

      <Form.Item
        label="Password"
        name="password"
        rules={[
          {
            required: true,
            message: 'Please input your password!',
          },
        ]}
      >
        <Input.Password prefix={<UnlockTwoTone twoToneColor="#000080" />} />
      </Form.Item>
      <Form.Item>
        <Button style={{ width: '100%', height: "36px", }} type="primary" htmlType="submit">
          Đăng nhập
        </Button>
      </Form.Item>
    </Form>
  );
};
export default LoginArea
