import { gql } from '@apollo/client';

const CREATE_USER = gql`
  mutation createUser($input: UserInput!) {
    createUser(input: $input)
  }
`

const LOGIN = gql`
  query login($input: LoginInput!) {
    login (input: $input)
  }
`

const GET_PLACES_BASIC = gql`
  query getPlacesBasic  {
      getPlacesBasic {
        label
        value
        children {
          value
          label
          children {
            value
            label
          }
        }
      }
    }
`

export {
  CREATE_USER,
  GET_PLACES_BASIC,
  LOGIN
}
