import jwt_decode from "jwt-decode"
const checkToken = () => {
  try {
    let token = localStorage.getItem("token");
    if (!token) return false
    var decoded = jwt_decode(token)
    console.log(decoded)
    if ((+decoded.exp - Date.now() / 1000) > 0) return true
  } catch {
    return false
  }
}

export default checkToken