import React, { useState } from 'react'
import { Layout, Menu, Image } from 'antd'
import {
    OrderedListOutlined, GroupOutlined, ShoppingCartOutlined, LogoutOutlined,
    BarChartOutlined, UserOutlined, SettingOutlined, BookOutlined
} from '@ant-design/icons';
import 'antd/dist/antd.css';
import './index.css';
import FormLogin from './login';
import checkToken from '../commonFunction/checkToken'
import logo2 from '../logo2.png'
import LoadMoreList from './listProduct/listProduct';
import ListOrder from './listOrder/listOrder';
import HomePage from './home';
import ProductGroup from './productGroup';
import Customer from './customer';
import Blogs from './blogs';
import Setting from './setting';

const { Header, Content, Footer, Sider } = Layout
const MaintApp = () => {
    const [isLogin, setLogin] = useState(checkToken())
    const [keyMenu, setkeyMenu] = useState({ contentHeaderTop: 'Trang tổng quan', key: '1' })
    const onClickMenu = ({ item, key, keyPath, domEvent }) => {
        console.log({ item, key, keyPath, domEvent })
        if(key === 'logout') {
            localStorage.removeItem('token');
            setLogin(false)
        }
        let objKeyMenu = {}
        switch (key) {
            case '1':
                objKeyMenu = { contentHeaderTop: 'Trang tổng quan', key }
                break;
            case '2':
                objKeyMenu = { contentHeaderTop: 'Đơn hàng', key }
                break;
            case '3':
                objKeyMenu = { contentHeaderTop: 'Sản phẩm', key }
                break;
            case '4':
                objKeyMenu = { contentHeaderTop: 'Nhóm sản phẩm', key }
                break;
            case '5':
                objKeyMenu = { contentHeaderTop: 'Khách hàng', key }
                break;
            case '6':
                objKeyMenu = { contentHeaderTop: 'Blog', key }
                break;
            case '7':
                objKeyMenu = { contentHeaderTop: 'Cấu hình', key }
                break;
            default:
                objKeyMenu = { contentHeaderTop: 'Trang Tổng quan', key: '1' }
        }
        setkeyMenu(objKeyMenu)
    }
    const ChoseItemMenuShow = (props) => {
        console.log('props ChoseItemMenuShow ', props)
        switch (props.keyMenu) {
            case '1':
                return <HomePage />
            case '2':
                return <ListOrder />
            case '3':
                return <LoadMoreList />
            case '4':
                return <ProductGroup />
            case '5':
                return <Customer />
            case '6':
                return <Blogs />
            case '7':
                return <Setting />
            default:
                return <LoadMoreList />
        }
    }

    return (
        !isLogin ? <FormLogin setLogin={setLogin} /> :
            <Layout>

                <Sider
                    breakpoint="lg"
                    collapsedWidth="0"
                    onBreakpoint={broken => {
                        console.log(broken);
                    }}
                    onCollapse={(collapsed, type) => {
                        console.log('object clickTrigger');
                        console.log(collapsed, type);
                    }}
                >
                    <div className="logo" >
                        <Image
                            preview={false}
                            width={"100%"}
                            height={"100%"}
                            src={logo2}
                        />
                    </div>
                    <Menu mode="inline" onClick={onClickMenu}
                        defaultSelectedKeys={['1']}
                    >
                        <Menu.Item key="1" icon={<BarChartOutlined />}>
                            Tổng quan
                        </Menu.Item>
                        <Menu.Item key="2" icon={<ShoppingCartOutlined />}>
                            Đơn hàng
                        </Menu.Item>
                        <Menu.Item key="3" icon={<OrderedListOutlined />}>
                            Sản phẩm
                        </Menu.Item>
                        <Menu.Item key="4" icon={<GroupOutlined />}>
                            Nhóm sản phẩm
                        </Menu.Item>
                        <Menu.Item key="5" icon={<UserOutlined />}>
                            Khách hàng
                        </Menu.Item>
                        <Menu.Item key="6" icon={<BookOutlined />}>
                            Blog
                        </Menu.Item>
                        <Menu.Item key="7" icon={<SettingOutlined />}>
                            Cấu hình
                        </Menu.Item>
                        <Menu.Item key="logout" icon={<LogoutOutlined />}>
                            Đăng Xuất
                        </Menu.Item>

                    </Menu>
                </Sider>
                <Layout>
                    <Header className="site-layout-sub-header-background"
                        style={{ background: '#FFC0CB', color: "purple", padding: 0 }}
                    >
                        <strong>
                            {keyMenu.contentHeaderTop}
                        </strong>

                    </Header>
                    <Content style={{ margin: '24px 16px 0' }}>
                        <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>

                            <ChoseItemMenuShow keyMenu={keyMenu.key} />

                        </div>
                    </Content>
                    <Footer style={{ textAlign: 'center' }}>Được thiết kế bởi Phan Xuân Đức</Footer>
                </Layout>
            </Layout>
    )
}

export default MaintApp


